/* <--------------------------- * SECTION1 * --------------------------- */
.saveReusableModal_container {
    position: relative;
    padding: 3.2rem 2.4rem;
    width: 41.4rem;
    height: 29.6rem;
    box-sizing: border-box;
}
.saveReusableModal_title {
    @include h_2();
    color: $neutral_700;
}
.saveReusableModal_desc {
    @include body_1();
    margin-top: 0.8rem;
    color: $neutral_300;
    white-space: nowrap;
}
.saveReusableModal_form {
    margin-top: 2.4rem;
    width: 100%;
    .saveReusableModal_error {
        color: $error;
        font: $fs_r_10;
    }
}
.saveReusableModal_label {
    @include subtitle_2();
    color: $neutral_600;
}
.saveReusableModal_input {
    margin-top: 0.8rem;
    width: 100%;
}
.saveReusableModal_btn {
    position: absolute;
    bottom: 1.6rem;
    right: 2.4rem;
    button {
        &:nth-of-type(1) {
            color: $primary_600 !important;
        }
        &:nth-of-type(2) {
            background: $primary_600;
            box-shadow: 0px 2px 6px rgba(26, 26, 26, 0.06), 0px 3px 15px rgba(26, 26, 26, 0.1);
            border-radius: 0.6rem;
        }
    }
}
/* <--------------------------- * SECTION1 * --------------------------- */
