/* <------------------------------------ **** CONSTANT IMPORT START **** ------------------------------------ */
/** Import all the reference constant after this line */
@import '~/Constant/base.scss';
/* <------------------------------------ **** CONSTANT IMPORT END **** ------------------------------------ */
/* <------------------------------------ **** SECTION1 MIXIN START **** ------------------------------------ */
/* <------------------------------------ **** SECTION1 MIXIN END **** ------------------------------------ */
/* <--------------------------- * SECTION1 * --------------------------- */
.input_wrap {
    font: $fs_r_14;
    display: inline-block;
    border-radius: 0.4rem;
    border: 0.1rem solid $neutral_200;
    box-sizing: border-box;
    text-indent: 1rem;
    padding-right: 1rem;
    transition: border 0.5s ease;
    &::placeholder {
        font: $fs_r_14;
        color: $not_activated;
    }
    &:hover {
        border-color: rgba($color: $primary_variant, $alpha: 0.5);
    }
    &[disabled]:hover {
        border-color: $line_light;
    }
    &:focus {
        outline: none;
        border-color: rgba($color: $primary_variant, $alpha: 1);
    }
}
.input_error {
    border: 0.1rem solid $danger_600;
}
/* <--------------------------- * SECTION1 * --------------------------- */
