/* <--------------------------- * SECTION1 * --------------------------- */
.importReusableModal_container {
    position: relative;
    padding: 3.2rem 2.4rem;
    width: 60rem;
    height: 41.2rem;
    box-sizing: border-box;
}
.importReusableModal_title {
    @include h_2();
    color: $neutral_700;
}
.importReusableModal_desc {
    @include body_1();
    margin-top: 0.8rem;
    color: $neutral_300;
    white-space: nowrap;
}
.importReusableModal_list {
    margin-top: 2rem;
    .check_wrap {
        margin: 1.05rem 0;
    }
    .importReusableModal_item_line {
        width: 100%;
        border-bottom: 0.1rem solid $neutral_200;
    }
}
.importReusableModal_empty {
    display: flex;
    flex-direction: column;
    align-items: center;
    img {
        width: 11rem;
    }
    .empty_description {
        @include body_1();
        color: $neutral_300;
    }
}
.importReusableModal_add {
    @include body_2();
    display: flex;
    align-items: center;
    margin-top: 0.4rem;
    color: $primary_600;
    cursor: pointer;
    svg {
        margin-right: 0.7rem;
        font-size: 1.4rem;
    }
}
.importReusableModal_scroll {
    .scroll_scrollBar__vertical {
        margin-right: -2rem;
    }
}
.importReusableModal_btn {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    padding-right: 2.4rem;
    position: absolute;
    bottom: 0;
    right: 0;
    width: 100%;
    height: 6.4rem;
    background: $background;
    box-shadow: 0px -2px 4px rgba(0, 0, 0, 0.08);
    border-radius: 0px 0px 10px 10px;
    box-sizing: border-box;
    button {
        font-size: 1.4rem !important;
        &:nth-of-type(1) {
            color: $primary_600 !important;
        }
        &:nth-of-type(2) {
            background: $primary_600;
            box-shadow: 0px 2px 6px rgba(26, 26, 26, 0.06), 0px 3px 15px rgba(26, 26, 26, 0.1);
            border-radius: 0.6rem;
        }
    }
}
/* <--------------------------- * SECTION1 * --------------------------- */
