/* <--------------------------- * SECTION1 * --------------------------- */
.codeExistedModal_container {
    position: relative;
    padding: 3.2rem 2.4rem;
    width: 41.4rem;
    height: 37.6rem;
    user-select: none;
    box-sizing: border-box;
}
.codeExistedModal_title {
    display: flex;
    > span {
        @include h_4();
        color: $neutral_600;
    }
    svg {
        margin-right: 1.2rem;
        font-size: 2.4rem;
        color: $auxiliary_600;
    }
}
.codeExistedModal_hint {
    @include body_1();
    margin-top: 1.2rem;
    color: $neutral_300;
}
.codeExistedModal_scroll {
    margin-top: 2.4rem;
}
.codeExistedModal_scrollBody {
    max-height: 12.8rem;
}
.codeExistedModal_options {
    width: 36.6rem;
    border: 0.1rem solid $neutral_300;
    border-radius: 0.4rem;
}
.codeExistedModal_optionItem {
    display: flex;
    height: 3.6rem;
    margin-bottom: -0.1rem;
    border-bottom: 0.1rem solid $neutral_300;
}
.codeExistedModal_code {
    width: 5.83rem;
    height: 3.6rem;
    line-height: 3.6rem;
    text-align: center;
    border-right: 0.1rem solid $neutral_300;
    overflow: hidden;
    &__repeat {
        color: red;
    }
}
.codeExistedModal_content {
    width: 100%;
    height: 3.6rem;
    padding-left: 1.57rem;
    line-height: 3.6rem;
}
.codeExistedModal_errorTip {
    @include label();
    margin-top: 1.2rem;
    color: $danger_600;
}
.codeExistedModal_btn {
    position: absolute;
    bottom: 1.6rem;
    right: 2.4rem;
    button {
        &:nth-of-type(1) {
            color: $primary_600 !important;
        }
        &:nth-of-type(2) {
            background: $primary_600;
            box-shadow: 0px 2px 6px rgba(26, 26, 26, 0.06), 0px 3px 15px rgba(26, 26, 26, 0.1);
            border-radius: 0.6rem;
        }
    }
}
/* <--------------------------- * SECTION1 * --------------------------- */
