/* <--------------------------- * SECTION1 * --------------------------- */
.optionPropertyModal_container {
    position: relative;
    padding: 3.2rem 2.6rem;
    width: 60rem;
    height: 42.6rem;
    box-sizing: border-box;
}
.optionPropertyModal_title {
    @include h_3();
    color: $neutral_600;
    svg {
        margin-right: 0.8rem;
        font-size: 2rem;
        color: $primary_600;
    }
}
.optionPropertyModal_content {
    margin-top: 2.6rem;
    padding-left: 3rem;
    padding-right: 3.4rem;
}
.optionPropertyModal_content_check {
    display: flex;
    align-items: center;
    .check_icon {
        margin-right: 0;
    }
    .check_wrap {
        margin-right: 1rem;
    }
}
.optionPropertyModal_dropdownContainer {
    position: relative;
    user-select: none;
}
.optionPropertyModal_dropdownContainer__select {
    .optionPropertyModal_selected {
        border-color: $primary_600;
        svg {
            transform: rotate(180deg);
        }
    }
}
.optionPropertyModal_checkFuture {
    margin-top: 1.6rem;
}
.optionPropertyModal_selected {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 1rem;
    padding: 0 1.2rem;
    width: 48.4rem;
    height: 4rem;
    border: 0.1rem solid $neutral_200;
    border-radius: 0.6rem;
    box-sizing: border-box;
    > span {
        display: flex;
        align-items: center;
    }
    cursor: pointer;
}
.optionPropertyModal_content_tip {
    .kite_body {
        @include body_3();
        display: flex;
        align-items: center;
        width: 21.2rem;
        height: 5.8rem;
        font-size: 1.2rem;
        > span {
            line-height: 1.6rem;
        }
        svg {
            margin-right: 0.4rem;
            font-size: 1.6rem;
        }
    }
}
.optionPropertyModal_list {
    position: absolute;
    top: 4.2rem;
    left: 0;
    box-sizing: border-box;
}
.optionPropertyModal_createScript {
    color: $primary_600;
    svg {
        margin-right: 0.5rem;
        font-size: 1.6rem;
    }
}

.optionPropertyModal_btn {
    position: absolute;
    bottom: 1.6rem;
    right: 2.4rem;
    button {
        &:nth-of-type(1) {
            color: $primary_600 !important;
        }
        &:nth-of-type(2) {
            background: $primary_600;
            box-shadow: 0px 2px 6px rgba(26, 26, 26, 0.06), 0px 3px 15px rgba(26, 26, 26, 0.1);
            border-radius: 0.6rem;
        }
    }
}
/* <--------------------------- * SECTION1 * --------------------------- */
