/* <--------------------------- * SECTION1 * --------------------------- */
.caseCaderItem_container {
    position: relative;
    display: flex;
    justify-content: space-between;
    @include body_2();
    display: flex;
    align-items: center;
    margin-top: 0.8rem;
    padding: 0 0.8rem;
    width: 100%;
    height: 2.8rem;
    color: $type;
    border-radius: 0.4rem;
    cursor: pointer;
    user-select: none;
    white-space: nowrap;
    box-sizing: border-box;
    &:hover {
        background: $primary_50;
        > .caseCaderItem_child {
            display: block;
        }
    }
    &__select {
        background: $primary_100;
        &:hover {
            background: $primary_100;
        }
    }
    &__disable {
        color: $neutral_300;
    }
}
.caseCaderItem_child {
    display: none;
    position: absolute;
    left: 100%;
    top: 0;
    padding-left: 1rem;
}
.caseCaderItem_arrow {
    font-size: 1.2rem !important;
}
/* <--------------------------- * SECTION1 * --------------------------- */
